import React, { useState } from "react"

function Addlistitem(props){
  var rnd = (new Date()).getTime();
  const [visib,setvisb] = useState(false);
  
  return (
          <div>
            <table>
            <tr >
            <td>
            
            <li style={{textDecoration: visib? "line-through":"none"}}>{props.detail}</li>
            
            
            </td>
            
            <td>
            <input type="checkbox" id={"cb2"+rnd} onClick={() =>{
              setvisb(!visib);
              setTimeout(function () {
                
                // props.func(props.id);
              }, 500);
              
              
            }}/>
            <label htmlFor={"cb2"+rnd}><div className="tick"></div></label>
            </td>
            </tr>
            </table>
          </div>
    ) 
  
}

export default Addlistitem;