import React, { useState } from "react";
import Addlistitem from "./Addlistitem"

function App() {
  const [listitem,setlistname] = useState("");
  const [list,setlist] = useState([]);

  function changed(event){
    var val = event.target.value
    setlistname(val);
    
  }
  function clicked(){
    if(listitem!==""){
      setlist((prev) =>{
        return [...prev,listitem]
      });
    }
    
    setlistname("");
  }

  function todelete(){
    
    setlist([]);
    
  }
  

  return (
    <div className="container">
      <div className="heading">
        <h1>To-Do List</h1>
      </div>
      <div className="form">
        <input type="text" onChange={changed} value={listitem}/>
        <button  onClick = {clicked}>
          <span><b>Add</b></span>
        </button>
      </div>
      <div className="content">
        <ul id= "table">
          {list.map((litem,index) => <Addlistitem key={index} id={index} detail={litem} func={todelete} />)}
        </ul>
      </div>
      <button className="delete" onClick={todelete}><span><i className="material-icons">delete</i></span></button>
    </div>
  );
}

export default App;
